@import "../../assets/variables";

.side-bar {
  min-width: 64px;
  background-color: #151b1f;
  padding: 36px 0 15px 0;

  @media (max-width: $max-tablet) {
    min-width: 80px;
  }

  @media (max-width: $max-mobile) {
    display: none;
  }
}

.language {
  position: fixed;
  bottom: 18px;
  left: 8px;
  cursor: pointer;
  font-size: 13px;
  font-weight: bold;
  color: $clr-main-accent;
  text-align: center;
  width: 48px;
  line-height: 46px;
  border: 1px solid rgba(0, 0, 0, 0);
  animation: fadeIn 0.5s;

  &:hover {
    border: 1px solid white;
    border-radius: 50%;
    color: white;
  }

  @media (max-width: $max-tablet) {
    left: 16px;
  }
  @media (max-width: $max-mobile) {
    z-index: 20;
    bottom: 16px;
    left: 16px;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0
  }

  100% {
    opacity: 1
  }
}