@import "../../../assets/variables";

.slider {
  flex: 0 1 50%;
  display: flex;
  position: relative;
  overflow: hidden;
  background-color: #151b1f;

  &__control {
    cursor: pointer;
    height: 100%;
    background-color: transparent;
    border: none;
    outline: none;
    width: 64px;
    min-width: 64px;
    z-index: 6;

    &:hover {
      background-color: rgba(14, 194, 97, 0.2);
    }

    @media (max-width: $max-laptop) {
      width: 32px;
      min-width: 32px;
    }
  }

  &__content-wrapper {
    padding: 0 32px;
    width: 100%;
  }

  &__dots {
    position: absolute;
    bottom: 32px;
    width: 100%;
    display: flex;
    justify-content: center;
  }

  &__dot {
    cursor: pointer;
    width: 9px;
    height: 9px;
    margin-right: 9px;
    border: none;
    border-radius: 50%;
    background-color: #fff;
    transition: background-color 250ms;

    &--active {
      background-color: $clr-main-accent;
    }
  }

  .img-preload {
    display: none;
  }
}
