@import "../../../assets/variables";

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Roboto, sans-serif;
  font-size: 18px;
  font-weight: 500;
  height: 48px;
  width: 250px;
  color: #fff;
  background-image: linear-gradient(
    90deg,
    $clr-main-accent 2.61%,
    #02935f 112.6%
  );
  background-blend-mode: darken;
  border: none;
  border-radius: 8px;
  cursor: pointer;

  &__preloader {
    width: 26px;
  }

  a {
    color: #fff;
    text-decoration: none;
  }

  &:focus,
  &:hover {
    outline: none;
    box-shadow: inset 0 0 100px 100px rgba(0, 0, 0, 0.1);
  }

  &:active {
    box-shadow: inset 0 0 100px 100px rgba(0, 0, 0, 0.2);
  }

  &:disabled {
    background-color: $clr-gray-light;
    background-image: none;
  }

  &__admin {
    height: 29px;
    width: 95px;
    border-radius: 4px;
    background-image: none;
    background-color: $clr-main-blue;
    font-weight: normal;
    font-size: 12px;
    line-height: 13px;
  }

  &__admin-cancel {
    background-color: #E9ECEF;
    color: $clr-blue-dark;
    margin-left: 14px;

    @media (max-width: $max-mobile) {
      margin-left: 7px;
    }
  }

  &__admin-delete {
    background-color: #CB3656;
  }

  &__login {
    width: 110px;
  }

  &__slide-1 {
    background-image: linear-gradient(90deg, #13493f 0%, #0c7b1b 100%);
  }

  &__slide-2 {
    background-image: linear-gradient(90deg, #132949 0%, #0c7b67 100%);
  }

  &__slide-3 {
    background-image: linear-gradient(90deg, #493013 0%, #7b0c3b 100%);
  }

  &__slide-4 {
    background-image: linear-gradient(90deg, #281349 0%, #720c7b 100%);
  }

  &__cancel {
    background-image: linear-gradient(90deg, #493013 0%, #7b0c3b 100%);
  }
}
